import styles from "../ui/styles/ProductsPage.module.css";
import AddProduct from "../ui/components/products/AddProductComponent";
import { useRouter } from "next/router";
import "../../public/css/products.css";

const ProductPage = () => {
  const router = useRouter();
  return (
    <div className="d-flex justify-content-between align-items-center flex-column">
      <div className="d-flex justify-content-between align-items-center flex-column">
        <header className="header-panel">
          <button
            id="menu-page-return"
            className="btn primaria position-fixed top-0 end-0 m-2 shadow"
            onClick={() => {
              router.push("/menuPage");
            }}
          >
            Voltar
          </button>
          <img className="img logo" src="/vl-store-logo-white.svg" />
        </header>

        <div className="mx-auto product-page d-flex justify-content-center align-items-center terciary p-4 flex-column rounded-5 white-light">
          <h3 className="text-center mb-4">Produtos</h3>
          <div className="row w-100 justify-content-between">
            <div className="p-0 col-12 col-md-12">
              <input
                className="input-form primaria w-100"
                type="text"
                placeholder="Digite o produto..."
              />
            </div>
            <button className="btn primaria col-12 col-md-3 mt-2">
              Pesquisar
            </button>
            <button className="btn primaria col-12 col-md-3 mt-2">
              Limpar
            </button>
            <button className="btn primaria col-12 col-md-3 mt-2">
              Adicionar produto
            </button>
          </div>

          <div className="row w-100 gap-3 mt-4 justify-content-center">
            <div className="col-12 col-md-3 product-card rounded-5 p-3 d-flex justify-content-center flex-column">
              <p className="mt-2 card-title text-center">
                Quadrinho Turma da Mônica
              </p>
              <p className="card-title text-center">Categoria: Vestuário</p>
              <p className="card-title text-center">
                Material: Papel reciclado
              </p>
              <p className="card-title text-center">Preço: R$ 29,90</p>
              <button id="product-detail" className="btn primaria w-100 mt-2">
                Detalhes
              </button>
            </div>

            <div className="col-12 col-md-3 product-card rounded-5 p-3 d-flex justify-content-center flex-column">
              <p className="mt-2 card-title text-center">
                Camiseta Básica Branca
              </p>
              <p className="card-title text-center">Categoria: Vestuário</p>
              <p className="card-title text-center">Material: Algodão</p>
              <p className="card-title text-center">Preço: R$ 49,90</p>
              <button id="product-detail" className="btn primaria w-100 mt-2">
                Detalhes
              </button>
            </div>

            <div className="col-12 col-md-3 product-card rounded-5 p-3 d-flex justify-content-center flex-column">
              <p className="mt-2 card-title text-center">
                Caneta Esferográfica Azul
              </p>
              <p className="card-title text-center">Categoria: Papelaria</p>
              <p className="card-title text-center">Material: Plástico</p>
              <p className="card-title text-center">Preço: R$ 2,50</p>
              <button id="product-detail" className="btn primaria w-100 mt-2">
                Detalhes
              </button>
            </div>
          </div>

          <div className="row w-100 gap-3 justify-content-center mt-4">
            <button className="btn col-3 primaria btn-paginacao">
              Anterior
            </button>
            <button className="btn col-3 primaria btn-paginacao">
              Próxima
            </button>
          </div>
        </div>
      </div>
      <AddProduct />
    </div>
  );
};

export default ProductPage;
